/* Sticky footer styles
-------------------------------------------------- */
html > body {
  /* Margin bottom by footer height */
  position: relative;
  min-height: 100vh;
  padding-bottom: 178px;
}

.footer {
  position: absolute;
  bottom: 0;
  padding-top: 15px;
  padding-right: 15px;
  padding-left: 15px;
  padding-bottom: 12px;
  width: 100%;
  /* Set the fixed height of the footer here */
  height: auto;
  background-color: #222;
}

.fa-btn {
  margin-right: 7px;
}

.fa-btn-contact {
  margin-right: 5px;
}

.footer-heading {
  color: #fff;
}

ul.list-unstyled > li > a {
  color: #9D9D9D;
  text-decoration: none;
}

ul.list-unstyled > li > a:hover {
  color: #fff;
}

span.devider {
  color: #9D9D9D;
}

.margin-fix {
  margin-top: -10px;
}

/*hyperlinks footer */
a.hyperlink-footer {
  color: #9D9D9D;
  text-decoration: none;
}

a.hyperlink-footer:hover {
  color: #fff;
}
/* end hyperlinks footer*/

/* Divider footer */
.footer-divider {
  color: #9D9D9D;
}
/* end divider footer */

.info-text-footer {
  color: #9D9D9D;;
}



.background {
  /* padding-top:75px; */
  background: #bdd732 url('../img/background.png') no-repeat fixed center 110px;
}
.font-heading {
  font-family: Allan, helvetica, arial, sans-serif;
