}
.color-white {
  background-color: #ffffff;
  padding: 6px;
  border: 1.2px solid #dddddd;
  border-radius: 4px;
  -webkit-box-shadow: 0 1px 1px rgba(0, 0, 0, 0.05);
  box-shadow: 0 1px 1px rgba(0, 0, 0, 0.05);
}
.color-kapoenen {
  background-color: #ffff00;
}
.color-welpen {
  background-color: #228b22;
}
.color-jonggivers {
  background-color: #f5821f;
}
.color-givers {
  background-color: #256f91;
}
.color-jins {
  background-color: #ed1c23;
}
.color-leiding {
  background-color: #663399;
}
.thumbnail {
  position: relative;
  overflow: hidden;
}
.caption {
  position: absolute;
  top: -100%;
  right: 0;
  background: rgba(66, 139, 202, 0.75);
  width: 100%;
  height: 100%;
  padding: 2%;
  text-align: center;
  color: #ffffff !important;
  z-index: 2;
  -webkit-transition: all 0.5s ease-in-out;
  -moz-transition: all 0.5s ease-in-out;
  -o-transition: all 0.5s ease-in-out;
  -ms-transition: all 0.5s ease-in-out;
  transition: all 0.5s ease-in-out;
}
.thumbnail:hover .caption {
  top: 0%;
}
@font-face {
  font-family: 'Scouts-en-Gidsen-Vlaanderen';
  src:url('../fonts/Scouts-en-Gidsen-Vlaanderen.eot?96su6b');
  src:url('../fonts/Scouts-en-Gidsen-Vlaanderen.eot?#iefix96su6b') format('embedded-opentype'),
  url('../fonts/Scouts-en-Gidsen-Vlaanderen.woff?96su6b') format('woff'),
  url('../fonts/Scouts-en-Gidsen-Vlaanderen.ttf?96su6b') format('truetype'),
  url('../fonts/Scouts-en-Gidsen-Vlaanderen.svg?96su6b#Scouts-en-Gidsen-Vlaanderen') format('svg');
  font-weight: normal;
  font-style: normal;
}

.scouts {
  font-family: 'Scouts-en-Gidsen-Vlaanderen';
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

